import { gsap } from 'gsap'
import { SplitText } from "gsap/SplitText"

gsap.registerPlugin(SplitText)

export default class animation {
    constructor() {

          gsap.from(".arrow", 2.8, {
            // y: "10%",
            opacity: 0,
            ease: "Slow",
            delay: 2.5,
          });

    }
}